






import { Component, Vue, Prop, Watch } from 'vue-property-decorator'

@Component({
  name: 'Countdown'
})
export default class Countdown extends Vue {
  @Prop({ default: 60, type: Number }) time!: number // 

  @Prop({ default: 1, type: Number }) step!: number // 

  @Prop({ type: Boolean }) switch!: boolean // 

  private timeDiff: number = 0 // 

  private timer: any = null // 

  // 
  private get computedStep() {
    return this.step * 1000
  }

  @Watch('switch', { immediate: true })
  onCountdownSwitch(val: boolean) {
    if (val && !this.timer) {
      this.timeDiff = this.time
      this.triggerTimer()
    } else {
      this.clearTimer()
    }
  }

  private beforeDestroy() {
    this.clearTimer()
  }

  // 
  clearTimer() {
    if (this.timer) clearTimeout(this.timer)
  }

  // 
  private triggerTimer() {
    this.timer = setTimeout(() => {
      this.timeDiff--
      if (this.timeDiff <= 0) {
        this.$emit('on-end')
      } else {
        this.$emit('on-countdown', this.timeDiff)
        this.triggerTimer()
      }
    }, this.computedStep)
  }
}
